import React,{useState,useEffect,useContext} from 'react';
import LoadingPage from '../load/LoadingPage';
import {useNavigate} from 'react-router-dom'
import {onAuthStateChanged} from 'firebase/auth'
import { AuthContext } from '../router/IURouter';
import {auth} from '../firebase/init'

export default function RootPage() {
  const [isSignedIn,setIsSignedIn]=useState(false)
  // authcontext get current user for auth
  const {currentUser}=useContext(AuthContext)
  
  // navigate for change location
  const nav=useNavigate()

  
  // online get user effect
  useEffect(()=>{
    onAuthStateChanged(auth,user=>{
      if(user){
        setIsSignedIn(true)
      }
      else{
        setIsSignedIn(false)
      }
    })
  },[])
  

  useEffect(() =>{
    if(currentUser===null && isSignedIn===false){
      nav('/sign',{replace:true,state:{'message':"noauth"}})
    }
    else{
      nav('/home',{replace:true,state:{'message':'auth'}})
    }
  },[isSignedIn])


}
