import React from 'react';
import { Button } from 'react-bootstrap';
import {useNavigate} from 'react-router-dom'


// all not found page 
export default function NotFound() {
  const nav=useNavigate()


  return (
    <div className="position-fixed top-0 start-0 w-100 h-100 bg-dark">
      <div className="position-absolute translate-middle  justify-content-center align-items-center top-50 start-50  text-center text-danger ">
        <h1>Not Found</h1>
        <Button variant='outline-danger' onClick={() =>{nav('/',{replace: true})}} >Home</Button>
      </div>
    </div>
  );
}
