import React,{useState,useContext,useEffect} from 'react';
import {Navbar,Container,Nav} from 'react-bootstrap'
import { NavLink } from 'react-router-dom';


// css file
import './homeheader.css'


// icons
import {BsFillSignpost2Fill} from 'react-icons/bs'

export default function HomeHeader({currentUser}) {


  return (
    <>
    <Navbar bg="dark" fixed='top' className='home-navbar' id="home-navbar" expand="md">
  <Container >
    <Navbar.Brand href="/"><span className='text-danger' style={{fontWeight:800}} >Netflix</span></Navbar.Brand>
    <Navbar.Toggle className='text-danger' aria-controls="basic-navbar-nav">
        <BsFillSignpost2Fill />
    </Navbar.Toggle>
    <Navbar.Collapse className='home-header-nav-container'  id="basic-navbar-nav">
      <Nav className="me-auto text-danger ">
          <NavLink to='/home' className={({isActive})=>isActive? "active-nav nav-link": "nav-link"}>Home</NavLink>
        <Nav.Link href="/watch/now">Watching</Nav.Link>
        <Nav.Link href="/type/movies">Movies</Nav.Link>
        <Nav.Link href="/watch/shorts">Shorts</Nav.Link>
        <Nav.Link href="/type/list">List</Nav.Link>
      </Nav>
      {currentUser && <Nav>
        <Nav.Link href="/user">{currentUser.displayName} {currentUser.photoURL!=='' ? <img src={currentUser.photoURL} alt='' style={{borderRadius:'50%'}} width={48} height={48} />:undefined} </Nav.Link>
      </Nav> }
    </Navbar.Collapse>
  </Container>
</Navbar>
    </>
  );
}
