// Import FirebaseAuth and firebase.
import React,{useState,useEffect} from 'react';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import firebase from 'firebase/compat/app';
import 'firebase/compat/auth';
import {auth} from '../firebase/init'
import {onAuthStateChanged} from 'firebase/auth'
import { useNavigate } from 'react-router-dom';


// coms
import LoadingPage from '../load/LoadingPage';


// withfireui.css file
import './withfireui.css'


// Configure FirebaseUI.
const uiConfig = {
  // Popup signin flow rather than redirect flow.
  signInFlow: 'popup',
  // Redirect to /signedIn after sign in is successful. Alternatively you can provide a callbacks.signInSuccess function.
  signInSuccessUrl: '/',
  // We will display Google and Facebook as auth providers.
  signInOptions: [
    firebase.auth.GoogleAuthProvider.PROVIDER_ID,
    firebase.auth.EmailAuthProvider.PROVIDER_ID
  ],
};

function WithFirebaseUI() {
    
    const [isSignedIn, setIsSignedIn] = useState(false);
    const [fetchLoading, setFetchLoading] = useState(true)

    // navigate for if user is signed in ,it can redirect to home page
    const nav=useNavigate()

      // Listen to the Firebase Auth state and set the local state.
    useEffect(() => {
        onAuthStateChanged(auth,user => {
        if(user){
            setIsSignedIn(true)
            setFetchLoading(false)
        }
        else{
            setIsSignedIn(false)
            setFetchLoading(false)
        }
    });
    }, []);

    if(fetchLoading===true){
        return(
            <LoadingPage />
        );
    }

    if(isSignedIn===false){
        return (
            <div className="position-fixed top-0 start-0 w-100 h-100 bg-dark">
            <div className="position-absolute top-50 start-50 w-75 h-75  translate-middle text-danger text-center d-flex flex-column justify-content-center border border-danger shadow-lg sign-inner-container" >
                <h1><img src={require('../source/favicon_for_iumovie.ico') }className='sign-header-img' alt="" /></h1>
            <p>Please sign-in:</p>
            <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={auth} />
                </div>
            </div>
        );
    }
    else{
        nav('/home' ,{replace: true})
    }
    
  
}

export default WithFirebaseUI;