import React from 'react';
import {useNavigate,useLocation} from 'react-router-dom'
import './watch.css'
import {BiArrowBack} from 'react-icons/bi'
import {Button} from 'react-bootstrap'

export default function WatchPage() {

  const {state}=useLocation()
  const nav=useNavigate()

  console.log(state)
  const {url}=state||{}
  return (
    <div className='position-fixed top-0 start-0 w-100 h-100 bg-light'>
      <div style={{zIndex:1}} className="position-absolute top-0 start-0" ><Button variant='outline-danger border-0' onClick={()=>{nav(-1)}}><i style={{fontSize:32}} class="bi bi-backspace-fill text-danger "></i></Button></div>
      <div className="watch-container">
      <div className="watch-inner-container">
      <video src={url} controls id='watch-video' ></video>
      </div>
      </div>
    </div>
  );
}
