import {Spinner,Container} from 'react-bootstrap'
import React ,{memo}from 'react'

function LoadingPage() {
  return (
    <>
    <Container fluid>
        <div className="position-fixed top-0 start-0 w-100 h-100 bg-dark text-light">
            <div className="position-absolute top-50 start-50 w-50 h-50 d-flex justify-content-center align-items-center translate-middle">
                <Spinner animation="border" size='lg' className='text-danger' role="status"></Spinner>
            </div>
        </div>
    </Container>    
    </>
  );
}

export default memo(LoadingPage);