import React,{useEffect,useState} from 'react';
import './homelist.css'

import {storage} from '../firebase/init'
import {listAll,ref,getDownloadURL} from 'firebase/storage'
import { Spinner } from 'react-bootstrap';

import {useNavigate} from 'react-router-dom'


export default function HomeList({type_name}) {

    const [allUrl, setAllUrl] = useState([]);

    const type_ref=ref(storage,type_name);

    const nav=useNavigate()

    useEffect(() =>{
        listAll(type_ref).then((result)=>{
            result.items.forEach(item =>
                getDownloadURL(item).then(url =>
                    setAllUrl(old=>[...old,url]))
                    )
                
        })
    },[])

    useEffect(() => {
        allUrl.forEach(item =>{
            console.log('item=> ',item)
        })
    },[allUrl])

    const redirectToWatch=(value)=>{
        nav('/watch',{state:{url:value}})
    }

    if(!allUrl) {
        return(
            <div className='home-list-container-loading position-relative bg-dark p-4'>
                <div className="home-list-inner-loading position-absolute top-50 start-50 translate-middle">
                <Spinner animation="border" variant='danger' role="status">
  <span className="visually-hidden">Loading...</span>
</Spinner>
                </div>
            </div>
        );
    }
    else{
        return (
            <div className='home-list-container '>
                {allUrl.map((value,index)=>{
                    return <div key={index} className=' mx-1 d-inline-block' style={{width:300,height:'100%'}}>
                        <video onClick={()=>{redirectToWatch(value)}} autoPlay muted src={value} width="100%" height="100%"></video>
                    </div>
                })}
            </div>
          );
    }

 
}
