import React,{useState,useEffect,createContext} from 'react';
import { BrowserRouter as Router,Routes,Route } from 'react-router-dom';
import {onAuthStateChanged} from 'firebase/auth'

// firebase auth
import {auth} from '../firebase/init'


// coms

import HomePage from '../home/HomePage'
import SignPage from '../sign/SignPage'
import WatchPage from '../watch/WatchPage'
import NotFound from '../notfound/NotFound'
import RootPage from '../root/RootPage';

// user pages
import UserPage from '../user/UserPage'
import UserEdit from '../user/UserEdit';
import UserHome from '../user/UserHome';
import UserHistory from '../user/UserHistory'


// sign coms
import WithFirebaseUI from '../sign/WithFireUI'
import LoadingPage from '../load/LoadingPage';
import ChangePage from '../sign/ChangePage'

const AuthContext=createContext();

// the component is router for this app, so it can be used for redirect base routes
export default function IURouter() {

  const [currentUser, setCurrentUser] = useState(null)
  const [fetchLoading, setFetchLoading] = useState(true)


 
  useEffect(() => {
    onAuthStateChanged(auth,user=>{
      if(user){
        setCurrentUser(user)
        setFetchLoading(false)
      }
      else{
        setCurrentUser(null)
        setFetchLoading(false)
      }
    })
  }, []);


  if(fetchLoading){
    return(<LoadingPage />);
  }


  return (
    <Router>
        <AuthContext.Provider value={{"currentUser":currentUser,"setCurrentUser":setCurrentUser,'name':'iu'}}>
          <Routes>
              <Route path='/' element={<RootPage />} />
              <Route path='/home' element={<HomePage />} />
              <Route path='/user' element={<UserPage />} >
                <Route index path='' element={<UserHome />} />
                <Route path='edit' element={<UserEdit />} />
                <Route path='history' element={<UserHistory />} />
              </Route>
              <Route path='/sign' element={<SignPage />}  >
                <Route index path='' element={<WithFirebaseUI />} />
                {/* <Route  path='change' element={<ChangePage />} /> */}
              </Route>
              <Route path='/watch' element={<WatchPage />}  />
              <Route path="*" element={<NotFound />} />
          </Routes>
        </AuthContext.Provider>
    </Router>
  );
}


export {AuthContext}