import React ,{useContext,useEffect} from 'react';
import {auth} from '../firebase/init'
import HomeHeader from './HomeHeader';
import { AuthContext } from '../router/IURouter';
import { useNavigate } from 'react-router-dom';
import NewPage from './NewPage';
import HomeList from './HomeList';


const HomePage = () => {
        // current user
        const {currentUser} = useContext(AuthContext)
        console.log(currentUser,'current user')
        // navigate
        const nav = useNavigate()
    
        useEffect(() => {
            if(currentUser ===null){
                nav('/sign',{replace:true})
            }
        },[])
    return (
        <div className='bg-dark'>
            <HomeHeader currentUser={currentUser} />
            <div id="home-main">
                <div className="home-news-page">
                    <NewPage />
                </div>
                <div className='py-2 text-danger'>
                    <h3 className=' px-2 border-bottom shadow-lg  border-danger'>Anime</h3>
                    <HomeList type_name='anime' />
                </div>
                <div className='py-2 text-danger'>
                    <h3 className=' px-2 border-bottom  shadow-lg border-danger'>Hollywood</h3>
                    <HomeList type_name='hollywood' />
                </div>
                <div className='py-2 text-danger'>
                    <h3 className=' px-2 border-bottom shodow-lg border-danger'>Drama</h3>
                    <HomeList type_name='anime' />
                </div>
            </div>

        </div>
    );
}

export default HomePage;
