import React,{useEffect,useState} from 'react';
import { storage } from '../firebase/init';
import {ref,getDownloadURL,listAll} from 'firebase/storage';
import {Button, Placeholder} from 'react-bootstrap'
import {BiMoviePlay} from 'react-icons/bi'

// newpage.css
import './newpage.css'


export default function NewPage() {
    const [videoUrl, setVideoUrl] = useState(null);
    const listRef=ref(storage,'new_movies')
    const [listUrl, setListUrl] = useState([]);

    useEffect(() => {
        listAll(listRef).then((result)=>{
            result.items.forEach(item =>
                getDownloadURL(item).then(url =>
                    setListUrl(old=>[...old,url]))
                    )
        })
    }, []);

    useEffect(() => {
        let ranNumber=Math.floor(Math.random()*(listUrl.length+1))
        setVideoUrl(listUrl[ranNumber])
    }, [listUrl]);

    if(videoUrl){
        return(
            <div className="position-relative bg-dark ">
                <div className="">
                <video src={videoUrl} muted  autoPlay  className='newpage-video'></video>
                </div>
                
            </div>
        );
    }
    else{
        return (
            <Placeholder  style={{width: '100%', height: '80vh'}}  animation="glow">
                <div className='newpage-img bg-dark' style={{width: '100%', height: '80vh'}}></div>
            </Placeholder>
        );
    }
}
